.mc {
  /* min-width: 35rem; */
  /* width: 78%; */
  min-height: 10rem;
  height: calc(100vh - 140px);
  overflow-x: hidden;
  overflow-y: auto;
  scroll-behavior: smooth;
  margin-bottom: 2rem;
}
.mc_outer_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mc_header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.mc_header > h6 {
  font-weight: 500;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
}
.mc_outer_header > img,
.mc_header > img {
  height: 1.2rem;
  width: 1.2rem;
  cursor: pointer;
}
.mc_step {
  padding-inline: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mcs_inner {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mcs_inner > h6 {
  font-weight: 500;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
}
.mcs_progress {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.mcs_circle {
  border-radius: 50%;
  height: 20px;
  position: relative;
  width: 20px;
  z-index: 11;
  background-color: var(--mcs-circle-bg);
}
.mcs_line {
  height: 5px;
  width: 100%;
  position: relative;
  z-index: 10;
  background-color: var(--mcs-circle-bg);
  margin-left: 100%;
  margin-top: -12px;
}
/* // for grab file ui  */
.mcuy_file {
  font-weight: 600;
  font-size: 28px;
  line-height: 15px;
  color: var(--t-head-color);
  text-align: center;
  margin-block: 3rem 5rem;
  letter-spacing: 1px;
}
.mcuyf_note {
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  color: var(--t-head-color);
  text-align: center;
  margin: 0;
}
.mcf_card {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mcf_container {
  /* width: 440px; */
  width: 50%;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 0.5px solid #001a72;
  border-radius: 20px;
  box-shadow: 0 2px 10px 0 rgba(39, 49, 58, 0.2);
}
.mcf_card_paragraph {
  font-weight: 400;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
  opacity: 0.6;
  margin: 0;
}
.mcfc_sample {
  margin-top: 3rem;
  font-weight: 400;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
  opacity: 0.4;
}
.mcf_drag_area {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
/* // for next style */
.mc_excel_container {
  /* margin-inline: 3rem; */
  width: 90%;
  margin-inline: auto;
  border-radius: 10px;
  box-shadow: 0 2px 15px 0 rgba(39, 49, 58, 0.15);
  /* width: 1000px; */
  background: #ebeff3;
  border: 0.5px solid #cacaca;
  padding-top: 11px;
}
.mc_heading,
.mc_content {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  /* gap: 1rem; */
  padding-left: 0.8rem;
}
.mc_heading > h6 {
  flex: 1 1;
  font-weight: 500;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
  width: var(--mc-width);
  text-align: center;
}

.mc_content {
  background: #ffffff;
}
.mc_content > h6 {
  flex: 1 1;
  font-weight: 500;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
  width: var(--mc-width);
  text-align: center;
  margin: 0;
}
.mcc_inner {
  flex: 1 1;
}
.mcc_inner > h6 {
  font-weight: 500;
  font-size: var(--font-14);
  line-height: 15px;
  color: var(--t-head-color);
  width: var(--mc-width);
  text-align: left;
  margin: 0;
  margin-bottom: 0.2rem;
}
.mcc_inner > p {
  font-weight: 400;
  font-size: var(--font-12);
  line-height: 15px;
  color: var(--t-head-color);
  width: var(--mc-width);
  text-align: left;
  margin: 0;
}
.mcfd_card {
  border-radius: 10px;
  box-shadow: 0 2px 10px 0 rgba(39, 49, 58, 0.2);
  margin: 0 auto 80px;
  min-height: 260px;
  padding: 40px 80px;
  width: 573px;
}
